import React, { Component } from 'react';
import '../App.css';


import table from "../image/tablebois.PNG";

class Cover1 extends Component {
  constructor() {
    super();
    this.state = {
      jcp1: true
    };
  }
  allo = () => {
    this.props.actionName()
  }



  jcp1 = () => {
    this.setState({
      jcp1: true,
      jcp2: false,
      jcp3: false,
    }
    )
    document.getElementById("jcp1").className = "numb"
    document.getElementById("jcp2").className = "numbOff"
    document.getElementById("jcp3").className = "numbOff"
  }
  jcp2 = () => {
    this.setState({
      jcp1: false,
      jcp2: true,
      jcp3: false,
    }
    )
    document.getElementById("jcp1").className = "numbOff"
    document.getElementById("jcp2").className = "numb"
    document.getElementById("jcp3").className = "numbOff"
  }
  jcp3 = () => {
    this.setState({
      jcp1: false,
      jcp2: false,
      jcp3: true,
    }
    )
    document.getElementById("jcp1").className = "numbOff"
    document.getElementById("jcp2").className = "numbOff"
    document.getElementById("jcp3").className = "numb"

  }


  render() {

    return (
      <div className="container white">
        <div className="box2 "   >

          <div className="centerAll"   >
            <div className=" "   >
            <hr className="lineSplit"/>
            <div className="JcpName "   >JCPERRAULT</div>
            <hr className="lineSplit"/>


              <div className="containerTextVideo">


                <div className="boxSize">
                  <div className="test2">
                  {!this.state.jcp3 &&<div  
                  
                  
                  className="test33" onClick={this.jcp3}> 
                  
                  <img id="" className="imgCover" src={table} alt="cover"  />
                  </div>}
                  {this.state.jcp3 &&
                   <iframe
                   className="test3"
                   src="https://player.vimeo.com/video/540287695?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479"
                   frameBorder="0"
                   allow="autoplay; fullscreen; picture-in-picture"
                   allowFullScreen title="demo v333">
                 </iframe>}

                 </div>


                </div>


                <div className="largeScreen2">
                  <div className="test2 ">
                  {!this.state.jcp2 &&<div   className="test33" onClick={this.jcp2}>
                  <img id="" className="imgCover" src={table} alt="cover"  />
                    </div>}
                  {this.state.jcp2 &&
                   <iframe
                   className="test3"
                   src="https://player.vimeo.com/video/540287695?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479"
                   frameBorder="0"
                   allow="autoplay; fullscreen; picture-in-picture"
                   allowFullScreen title="demo v333">
                 </iframe>}
                  </div>
                </div>

                <div className="largeScreen3">
                  <div className="test2">
                  {!this.state.jcp1 &&<div   className="test33" onClick={this.jcp1}>
                  <img id="" className="imgCover" src={table} alt="cover"  />
                    </div>}
                  {this.state.jcp1 &&
                   <iframe
                   className="test3"
                   src="https://player.vimeo.com/video/540287695?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479"
                   frameBorder="0"
                   allow="autoplay; fullscreen; picture-in-picture"
                   allowFullScreen title="demo v333">
                 </iframe>}
                    </div>
                </div>



                <div>

                {this.state.jcp1 &&
                  <div>
                    <div className="titreJcp"  >Commandite Star Académie
                   

             </div>
                    <div className="texteJcp"  >
                    Montage vidéo à partir donner un peu plus de qualité, nous avons ajouté de l’animation/effets dans certaines images pour rendre le tout plus dynamique.
</div>
                  </div>}

                {this.state.jcp2 &&
                    <div>
                    <div className="titreJcp"  >Palliser 
                   

             </div>
                <div className="texteJcp"  >Montage vidéo à partir d’images fixes (photos) fournies par le client. Pour donner un peu plus de qualité, nous avons ajouté de l’animation/effets dans certaines images pour rendre le tout plus dynamique.
             </div>
             </div>}
           


                {this.state.jcp3 &&     <div>
                    <div className="titreJcp"  >Natuzzi (Remontage 30s)
                   

             </div>
                <div className="texteJcp"  >Nous avons été mandaté de produire une publicité 30s à partir d’une vidéo existante de 55 secondes. Nous avons dû recréer une histoire à partir d’images existantes et créer une ambiance qui fait rêver avec des effets sonores.
             </div>
             </div>}


</div>     </div>

<div className="numberContainer">

<div id="jcp1" className="numb" onClick={this.jcp1} >0  </div>
<div id="jcp2" className="numbOff" onClick={this.jcp2} >0  </div>
<div id="jcp3" className="numbOff" onClick={this.jcp3}>0  </div>

</div>


            </div>
          </div>
        </div>
      </div>
    )
  }
}
export default Cover1;
